<template>
  <div class="page-checkbox">
    <div class="page-title">Checkbox </div>
    <div class="page-sub-title">  v-model </div>
    <Checkbox v-model="single" size="large">Checkbox</Checkbox>
    <p>{{single}}</p>

    <div class="page-sub-title">true-value  false-value</div>
    <Checkbox v-model="checkedValue" true-value="" false-value="">{{checkedValue}}</Checkbox>

    <div class="page-sub-title">
      CheckboxGroup
      Checkbox  label 
       Checkbox  label 
    </div>
    <CheckboxGroup v-model="fruit">
      <Checkbox label=""></Checkbox>
      <Checkbox disabled label=""></Checkbox>
      <Checkbox label=""></Checkbox>
      <Checkbox label=""></Checkbox>
    </CheckboxGroup>
    <p>{{fruit}}</p>

    <div class="page-sub-title"></div>
    <Checkbox v-model="checked" :disabled="disabled">
      <span v-if="checked">Checked</span>
      <span v-else>Unchecked</span>
      -
      <span v-if="!disabled">Usable</span>
      <span v-else>Disabled</span>
    </Checkbox>
    <br />
    <Button type="primary" @click="checked = !checked">
      <span v-if="!checked">Checked</span>
      <span v-else>Unchecked</span>
    </Button>
    <Button type="primary" @click="disabled = !disabled">
      <span v-if="disabled">Usable</span>
      <span v-else>Disabled</span>
    </Button>

    <div class="page-sub-title"></div>
    <p> indeterminate </p>
    <div style="border-bottom: 1px solid #e9e9e9;padding-bottom:6px;margin-bottom:6px;">
      <Checkbox
        :indeterminate="indeterminate"
        :value="checkAll"
        @click.prevent.native="handleCheckAll"
      ></Checkbox>
    </div>
    <CheckboxGroup v-model="checkAllGroup" @on-change="checkAllGroupChange">
      <Checkbox label=""></Checkbox>
      <Checkbox label=""></Checkbox>
      <Checkbox label=""></Checkbox>
    </CheckboxGroup>
    <p style="margin-top:200px;"></p>
  </div>
</template>
<script>
export default {
  data() {
    return {
      single: false,
      fruit: [""],
      checkedValue: "",
      checked: true,
      disabled: false,
      indeterminate: true,
      checkAll: false,
      checkAllGroup: ["", ""]
    };
  },
  methods: {
    handleCheckAll() {
      if (this.indeterminate) {
        this.checkAll = false;
      } else {
        this.checkAll = !this.checkAll;
      }
      this.indeterminate = false;

      if (this.checkAll) {
        this.checkAllGroup = ["", "", ""];
      } else {
        this.checkAllGroup = [];
      }
    },
    checkAllGroupChange(data) {
      if (data.length === 3) {
        this.indeterminate = false;
        this.checkAll = true;
      } else if (data.length > 0) {
        this.indeterminate = true;
        this.checkAll = false;
      } else {
        this.indeterminate = false;
        this.checkAll = false;
      }
    }
  }
};
</script>