    <p></p>

    <div class="page-sub-title"></div>
    <p>
      
      <br />
      <br />
    </p>
    <Carousel v-model="value1" loop>
      <CarouselItem>
        <div class="demo-carousel">1</div>
      </CarouselItem>
      <CarouselItem>
        <div class="demo-carousel">2</div>
      </CarouselItem>
      <CarouselItem>
        <div class="demo-carousel">3</div>
      </CarouselItem>
      <CarouselItem>
        <div class="demo-carousel">4</div>
      </CarouselItem>
    </Carousel>

    <div class="page-sub-title"></div>
    <p>
       autoplay  autoplay-speed 
      <br />
      <br />
    </p>
    <Carousel autoplay v-model="value2" loop>
      <CarouselItem>
        <div class="demo-carousel">1</div>
      </CarouselItem>
      <CarouselItem>
        <div class="demo-carousel">2</div>
      </CarouselItem>
      <CarouselItem>
        <div class="demo-carousel">3</div>
      </CarouselItem>
      <CarouselItem>
        <div class="demo-carousel">4</div>
      </CarouselItem>
    </Carousel>

    <div class="page-sub-title"></div>
    <p>
      
      <br />
      <br />
    </p>
    <Form :model="setting" :label-width="100">
      <FormItem label="">
        <ISwitch v-model="setting.autoplay">
          <span slot="open"></span>
          <span slot="close"></span>
        </ISwitch>
      </FormItem>
      <FormItem label="">
        <ISwitch v-model="setting.radiusDot">
          <span slot="open"></span>
          <span slot="close"></span>
        </ISwitch>
      </FormItem>
      <FormItem label="">
        <Slider v-model="setting.autoplaySpeed" :min="300" :max="10000" :step="100"></Slider>
      </FormItem>
      <FormItem label="">
        <RadioGroup v-model="setting.dots" type="button">
          <Radio label="inside"></Radio>
