<template>
  <div>
    <div class="page-title">Tag </div>
    <p></p>

    <div class="page-sub-title"></div>
    <p>closable on-close </p>
    <Tag></Tag>
    <Tag></Tag>
    <Tag v-if="show" closable @on-close="handleClose"></Tag>

    <div class="page-sub-title"></div>
    <p> type  border  dot  border dot</p>
    <Tag type="border"></Tag>
    <Tag type="border" closable></Tag>
    <Tag type="dot"></Tag>
    <Tag type="dot" closable></Tag>

    <div class="page-sub-title"></div>
    <p></p>
    <Tag color="default">default</Tag>
    <Tag color="primary">primary</Tag>
    <Tag color="success">success</Tag>
    <Tag color="error">error</Tag>
    <Tag color="warning">warning</Tag>
    <Tag color="magenta">magenta</Tag>
    <Tag color="red">red</Tag>
    <Tag color="volcano">volcano</Tag>
    <Tag color="orange">orange</Tag>
    <Tag color="gold">gold</Tag>
    <Tag color="yellow">yellow</Tag>
    <Tag color="lime">lime</Tag>
    <Tag color="green">green</Tag>
    <Tag color="cyan">cyan</Tag>
    <Tag color="blue">blue</Tag>
    <Tag color="geekblue">geekblue</Tag>
    <Tag color="purple">purple</Tag>
    <Tag color="#FFA2D3">Custom Color</Tag>
    <br />
    <br />
    <Tag type="border" closable color="primary"></Tag>
    <Tag type="border" closable color="success"></Tag>
    <Tag type="border" closable color="error"></Tag>
    <Tag type="border" closable color="warning"></Tag>
    <br />
    <br />
    <Tag type="dot" closable color="primary"></Tag>
    <Tag type="dot" closable color="success"></Tag>
    <Tag type="dot" closable color="error"></Tag>
    <Tag type="dot" closable color="warning"></Tag>

    <div class="page-sub-title"></div>
    <p> checkable checked </p>
    <Tag checkable color="primary"></Tag>
    <Tag checkable color="success"></Tag>
    <Tag checkable color="error"></Tag>
    <Tag checkable color="warning"></Tag>

    <div class="page-sub-title"></div>
    <p></p>
    <Tag
      v-for="item in count"
      :key="item"
      :name="item"
      closable
      @on-close="handleClose2"
    >{{ item + 1 }}</Tag>
    <Button icon="ios-add" type="dashed" size="small" @click="handleAdd"></Button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      show: true,
      count: [0, 1, 2]
    };
  },
  methods: {
    handleClose() {
      this.show = false;
    },
    handleAdd() {
      if (this.count.length) {
        this.count.push(this.count[this.count.length - 1] + 1);
      } else {
        this.count.push(0);
      }
    },
    handleClose2(event, name) {
      const index = this.count.indexOf(name);
      this.count.splice(index, 1);
    }
  }
};
</script>