<template>
  <div>
    <div class="page-title">Time </div>
    <p></p><br>

    <div class="page-sub-title"></div>
    <p> Date<br></p>
    <div>
        <Time :time="time1" />
            <br>
        <Time :time="time2" />
    </div>

    <div class="page-sub-title"></div>
    <p> 60 <br></p>
    <Time :time="time3" :interval="1" />

    <div class="page-sub-title"></div>
    <p><br></p>
        <Time :time="time2" />
        <br>
        <Time :time="time2" type="date" />
        <br>
        <Time :time="time2" type="datetime" />

    <div class="page-sub-title"></div>
    <p> hash <br></p>
    <Time :time="time2" hash="#hash" />
  </div>
</template>
<script>
    export default {
        data () {
            return {
                time1: (new Date()).getTime() - 60 * 3 * 1000,
                time2: (new Date()).getTime() - 86400 * 3 * 1000,
                time3: new Date()
            }
        }
    }
</script>