<template>
  <div style="width:420px;padding-bottom:200px">
    <div class="page-title">Input </div>
    <p> input  textarea</p>

    <div class="page-sub-title"></div>
    <p> v-model  style  100%</p>
    <Input v-model="value" placeholder="Enter something..." style="width: 300px" />

    <div class="page-sub-title"></div>
    <p>;sizelargesmall</p>
    <Input v-model="value1" size="large" placeholder="large size" />
    <br />
    <br />
    <Input v-model="value2" placeholder="default size" />
    <br />
    <br />
    <Input v-model="value3" size="small" placeholder="small size" />

    <div class="page-sub-title"></div>
    <p> clearable </p>
    <Input v-model="value14" placeholder="Enter something..." clearable style="width: 200px" />

    <div class="page-sub-title">Icon</div>
    <p> icon  on-click </p>
    <Input
      v-model="value4"
      icon="ios-clock-outline"
      placeholder="Enter something..."
      style="width: 200px"
      @on-click="onIconClick"
    />

    <div class="page-sub-title"></div>
    <p> icon prefix  suffix  slot </p>
    <div>
      Props
      <Input prefix="ios-contact" placeholder="Enter name" style="width: auto" />
      <br />
      <Input suffix="ios-search" placeholder="Enter text" style="width: auto" />
    </div>
    <div style="margin-top: 6px">
      Slots
      <Input placeholder="Enter name" style="width: auto">
        <Icon type="ios-contact" slot="prefix" />
      </Input>
      <br />
      <Input placeholder="Enter text" style="width: auto">
        <Icon type="ios-search" slot="suffix" />
      </Input>
    </div>

    <div class="page-sub-title"></div>
    <p> search </p>
    <Input search placeholder="Enter something..." @on-search="onSearch"/>
    <br />
    <br />
    <Input search enter-button placeholder="Enter something..." @on-search="onSearch"/>
    <br />
    <br />
    <Input search enter-button="Search" placeholder="Enter something..." @on-search="onSearch"/>

    <div class="page-sub-title"></div>
    <p> type  textarea  rows </p>
    <Input v-model="value5" type="textarea" placeholder="Enter something..." />
    <br />
    <br />
    <Input v-model="value6" type="textarea" :rows="4" placeholder="Enter something..." />

    <div class="page-sub-title"></div>
    <p> autosizeautosize</p>
    <Input v-model="value7" type="textarea" :autosize="true" placeholder="Enter something..." />
    <br />
    <br />
    <Input
      v-model="value8"
      type="textarea"
      :autosize="{minRows: 2,maxRows: 5}"
      placeholder="Enter something..."
    />

    <div class="page-sub-title"></div>
    <p>disabled</p>
    <Input v-model="value9" disabled placeholder="Enter something..." />
    <br />
    <br />
    <Input v-model="value10" disabled type="textarea" placeholder="Enter something..." />

    <div class="page-sub-title"></div>
    <p> slot </p>
    <Input v-model="value11">
      <span slot="prepend">http://</span>
      <span slot="append">.com</span>
    </Input>
    <br />
    <Input v-model="value12">
      <Select v-model="select1" slot="prepend" style="width: 80px">
        <Option value="http">http://</Option>
        <Option value="https">https://</Option>
      </Select>
      <Select v-model="select2" slot="append" style="width: 70px">
        <Option value="com">.com</Option>
        <Option value="org">.org</Option>
        <Option value="io">.io</Option>
      </Select>
    </Input>
    <br />
    <Input v-model="value13">
      <Select v-model="select3" slot="prepend" style="width: 80px">
        <Option value="day">Day</Option>
        <Option value="month">Month</Option>
      </Select>
      <Button slot="append">
        <Icon type="ios-search"/>
      </Button>
    </Input>

    <div class="page-sub-title"></div>
    <p> Poptip </p>
    <Poptip trigger="focus">
      <Input v-model="value15" prefix="social-usd" placeholder="Enter number" style="width: 120px" />
      <div slot="content">{{ formatNumber }}</div>
    </Poptip>
  </div>
</template>
<script>
export default {
  data() {
    return {
      value: "",
      value1: "",
      value2: "",
      value3: "",
      value4: "",
      value5: "",
      value6: "",
      value7: "",
      value8: "",
      value9: "",
      value10: "",
      value11: "",
      value12: "",
      value13: "",
      select1: "http",
      select2: "com",
      select3: "day",
      value14: "Hello World",
      value15: ""
    };
  },
  computed: {
    formatNumber() {
      if (this.value15 === "") return "Enter number";
      function parseNumber(str) {
        const re = /(?=(?!)(d{3})+$)/g;
        return str.replace(re, ",");
      }
      return parseNumber(this.value15);
    }
  },
  methods: {
    onIconClick() {
      alert('')
    },
    onSearch(value) {
      console.log(`${value}`)
    }
  }
};
</script>
