<template>
  <div>
    <div class="page-title">Message </div>
    <Button @click="openInfo"></Button>
    <Button @click="openSuccess"></Button>
    <Button @click="openWarning"></Button>
    <Button @click="openError"></Button>
    <Button @click="openLoading"></Button>
    <Button @click="openCanClose"></Button>
    <Button @click="openRenderFns"></Button>
  </div>
</template>
<script>
export default {
  methods: {
    openInfo() {
      this.$Message.info('')
    },
    openSuccess() {
      this.$Message.success('')
    },
    openWarning() {
      this.$Message.warning('')
    },
    openError() {
      this.$Message.error('')
    },
    openLoading() {
      let cs = this.$Message.loading({
        content: ''
      })
    },
    openCanClose() {
      this.$Message.error({
        content: '',
        closable: true
      })
    },
    openRenderFns() {
      this.$Message.error({
        closable: true,
        render: h => {
          return h('b', '')
        }
      })
    }
  }
}
</script>
